import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { deleteItem } from '../store';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import styles from './Cart.module.scss'; // SCSS  import

function Cart() {
    const cart = useSelector((state) => state.cart);
    const dispatch = useDispatch();

    const totalPrice = cart.reduce((total, item) => total + item.price, 0);

    return (
        <>
            <Header />
            <div className={styles.cartContainer}>
                <h2></h2>
                {cart.length === 0 ? (
                    <p className={styles.emptyCart}> .</p>
                ) : (
                    <>
                        <table>
                            <thead>
                                <tr>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {cart.map((item) => (
                                    <tr key={item.id}>
                                        <td>
                                            <img src={`${process.env.PUBLIC_URL}/img/room/${item.imgurl}`} alt={item.name} />
                                        </td>
                                        <td>{item.name}</td>
                                        <td>{item.startDate} ~ {item.endDate}</td>
                                        <td> {item.adults},  {item.children},  {item.infants}</td>
                                        <td>{item.price.toLocaleString()}</td>
                                        <td>
                                            <button className={styles.deleteBtn} onClick={() => dispatch(deleteItem(item.id))}></button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                        <div className={styles.totalPrice}>
                            <strong> : </strong>{totalPrice.toLocaleString()}
                        </div>
                    </>
                )}
            </div>
            <Footer />
        </>
    );
}

export default Cart;
