import React, { useState } from 'react';
import { Button, Table, Form, Alert } from 'react-bootstrap';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';

const FruitBoard = () => {

    //    
    const [boardList, setBoardList] = useState([
        { no: "1", title: '4    ?', description: " 4     ?", viewCount: 1 },
        { no: "2", title: '   ?', description: ' 1  ?', viewCount: 2 },
        { no: "3", title: '   ?', description: '5      ?', viewCount: 1 },
        { no: "4", title: '  ?', description: '    ?', viewCount: 1 }
    ]);

    // UI 
    const [listOk, setListOk] = useState(true);//  
    const [readOk, setReadOk] = useState(false);//  
    const [writeOk, setWriteOk] = useState(false); //  
    const [editOk, setEditOk] = useState(false);//  
    const [boardInfo, setBoardInfo] = useState({}); //      (,  )  


    //   
    const [title, setTitle] = useState('');
    const [description, setDescription] = useState('');

    //   
    const [editNo, setEditNo] = useState(null); //   
    const [editTitle, setEditTitle] = useState('');
    const [editDescription, setEditDescription] = useState('');

    //   
    const [errorMessage, setErrorMessage] = useState('');


    //   
    const boardListView = () => {
        setReadOk(false);
        setWriteOk(false);
        setEditOk(false);
        setListOk(true);
    };

    //  
    const boardRead = (no) => {
        setListOk(false);
        setWriteOk(false);
        setEditOk(false);
        setReadOk(true);
        
        //  ,   1 
        const updatedList = boardList.map(b =>
            b.no === no ? { ...b, viewCount: b.viewCount + 1 } : b
        );
        setBoardList(updatedList); // 1   
        
        const selectedBoard = boardList.find(b => b.no === no); //    
        setBoardInfo(selectedBoard); //        
    };

    //    
    const boardWrite = () => {
        setListOk(false);
        setWriteOk(true);
    };

    //   
    const boardSave = () => {
        //     
        if (title.trim() === '' || description.trim() === '') {
            setErrorMessage('   !');
            return;
        }
        
        //   
        const newBoard= {
            no: (boardList.length + 1).toString(),
            title: title,
            description: description,
            viewCount: 0 //   0
        };
        setBoardList([...boardList, newBoard]);
        setTitle('');
        setDescription('');
        setErrorMessage(''); //   
        boardListView(); //      
    };

    //  
    const boardDelete = (no) => {
        //   `no`   
        const updatedList = boardList.filter(b => b.no !== no.toString());  // no.toString() 
        setBoardList(updatedList); 
        boardListView(); //     
    };

    //    
    const boardEdit = (no) => {
        setEditOk(true);
        setListOk(false);
        
        const boardToEdit = boardList.find(b => b.no === no);  //     (, ) 
        setEditNo(boardToEdit.no);   //  
        setEditTitle(boardToEdit.title);  //  
        setEditDescription(boardToEdit.description);  //    .
    };

    //   
    const updateBoard = () => {
        const updatedBoardList = boardList.map(b =>  //       
            b.no === editNo ? { ...b, title: editTitle, description: editDescription } : b
        );
        setBoardList(updatedBoardList);   //   
        boardListView();  //     
    };

    return (
        <>
            <Header/>
            <div className="container" style={{ margin: "80px auto" }}>

                <h3>QnA</h3>

                {/* QnA   */}
                {listOk && (
                    <div style={{ marginTop: "30px" }}>
                        <Table striped bordered hover className="text-center">
                            <thead>
                                <tr>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {boardList.slice().reverse().map(board => (
                                    <tr key={board.no}>
                                        <td>{board.no}</td>
                                        <td style={{ cursor: 'pointer',  textAlign: 'left' }} onClick={() => boardRead(board.no)}>
                                            {board.title}
                                        </td>
                                        <td style={{ cursor: 'pointer',  textAlign: 'left' }} onClick={() => boardRead(board.no)}>
                                            {board.description}
                                        </td>
                                        <td>{board.viewCount}</td> {/*   */}
                                        <td>
                                            <Button variant="outline-primary" onClick={() => boardRead(board.no)}>
                                                
                                            </Button>
                                            <Button variant="outline-success" onClick={() => boardEdit(board.no)} style={{ marginLeft: "10px" }}></Button>
                                            <Button variant="outline-danger" onClick={() => boardDelete(board.no)} style={{ marginLeft: "10px" }}>
                                                
                                            </Button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </Table>
                        <div className="d-flex justify-content-end">
                            <Button variant="primary" onClick={boardWrite}>
                                 
                            </Button>
                        </div>
                    </div>
                )}

                {/*   */}
                {readOk && (
                    <div>
                        <h5 style={{textAlign: "left" }}>{boardInfo.title}</h5>
                        <hr></hr>
                        <p  style={{textAlign: "left" }}>{boardInfo.description}</p>
                        <br></br>
                        <div style={{ textAlign: "right"}}>
                            <Button variant="secondary" onClick={boardListView}  style={{textAlign: "right" }}>
                                
                            </Button>
                        </div>
                    </div>
                )}

                {/*     */}
                {writeOk && (
                    <div style={{ marginTop: "30px" }}>
                        <h5  style={{textAlign: "left" }}>  </h5>

                        {/*    */}
                        {errorMessage && (
                            <Alert variant="danger">{errorMessage}</Alert>
                        )}

                        <Form.Group controlId="formName">
                            <Form.Control
                                type="text"
                                value={title}
                                onChange={(e) => setTitle(e.target.value)}
                                placeholder=" "
                            />
                        </Form.Group>
                        <Form.Group controlId="formDescription" style={{ marginTop: "30px" }}>
                            <Form.Control
                                as="textarea"
                                rows={3}
                                value={description}
                                onChange={(e) => setDescription(e.target.value)}
                                placeholder=" "
                            />
                        </Form.Group>
                        <br></br>
                        <div style={{ textAlign: "right"}}>
                            <Button variant="primary" onClick={boardSave} style={{ marginRight: "10px" }}></Button>
                            <Button variant="secondary" onClick={boardListView}></Button>
                        </div>
                    </div>
                )}

                {/*   */}
                {editOk && (
                    <div style={{ marginTop: "30px" }}>
                        <h5 style={{textAlign: "left" }}> </h5>
                        <Form.Group controlId="formEditName">
                            <Form.Control
                                type="text"
                                value={editTitle}
                                onChange={(e) => setEditTitle(e.target.value)}
                                placeholder=" "
                            />
                        </Form.Group>
                        <br></br>
                        <Form.Group controlId="formEditDescription">
                            <Form.Control
                                as="textarea"
                                rows={3}
                                value={editDescription}
                                onChange={(e) => setEditDescription(e.target.value)}
                                placeholder=" "
                            />
                        </Form.Group>
                        <br></br>
                        <div style={{ textAlign: 'right' }}>
                            <Button variant="outline-success" onClick={updateBoard} style={{ marginRight: "10px" }}></Button>
                            <Button variant="outline-info" onClick={boardListView}></Button>
                        </div>
                    </div>
                )}

            </div>
            <Footer/>
        </>
    );
};

export default FruitBoard;
